/* Styles intended only for the front.*/
html {
	scroll-behavior: smooth;
}

@media screen and (prefers-reduced-motion: reduce) {
	html {
		scroll-behavior: auto;
	}
}

body {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}

/* Variables */
:root {
	--background-default: #fafafa;
	--action-main: rgba(14, 13, 15, 0.64);
	--action-main-svg: #0e0d0f;
	--divider-main: rgba(14, 13, 15, 0.08);
	--text-primary: #0e0d0f;
	--secondary-48: rgba(250, 250, 250, 0.48);
	--white-contrast: #0a090a;
	--purple-main: #6f76c0;
}

.dark-mode-playstore {
	--background-default: #0e0d0f;
	--action-main: rgba(255, 255, 255, 0.64);
	--action-main-svg: rgb(255, 255, 255);
	--divider-main: rgba(255, 255, 255, 0.12);
	--text-primary: #ffffff;
	--secondary-48: rgba(14, 13, 15, 0.6);
	--white-contrast: #ffffff;
}

body {
	font-family: Urbanist;
	font-style: normal;
}

.wp-block-create-block-block-header {
	.wp-block-navigation-submenu {
		margin-top: 0;
		gap: 32px;
		color: var(--text-primary, #0e0d0f);
		position: static;

		.wp-block-navigation__submenu-container {
			position: absolute;
			left: 0;
			right: 0;
			top: 100%;
			width: 100%;
			min-height: 52px;
			background: var(--secondary-48, rgba(250, 250, 250, 0.48));
			color: var(--white-contrast, #0a090a);
			display: flex;
			flex-direction: row;
			align-items: center;
			gap: 32px;
			padding-left: 268px;
			padding-right: 268px;
		}
	}
}
